.nav {
  display: inline-flex;
  position: relative;
  overflow: hidden;
  align-items: center;
  justify-content: center;
  right: 0;
  width: 99.9vw;
  height: 12vh;
  background-color: #fff;
  border-radius: 55px;
  box-shadow: 0 10px 40px rgba(159, 162, 177, .8);
}

.nav-item:active{
  transform: scale(0.89);
}

.nav-item {
  color: #83818c;
  padding: 4vw;
  text-decoration: none;
  transition: .3s;
  margin: 0 6px;
  z-index: 1;
  font-family: Georgia,serif;
  font-weight: 700;
  position: relative;
  font-size: 3.5vh;
}

.nav-item:before {
    content: "";
    position: absolute;
    bottom: -6px;
    left: 0;
    width: 100%;
    height: 29px;
    background-color: aqua;
    border-radius: 8px 8px 0 0;
    opacity: 0;
    transition: .3s;
  }

  .nav:hover{
    transform: scale(1.01);
  }


.nav-item:not(.is-active):hover:before {
  opacity: 1;
  bottom: 0;
  
}


.nav-item:not(.is-active):hover { color: rgb(0, 0, 0)}

.nav-indicator {
  position: absolute;
  left: 0;
  bottom: 0;
  height: 4px;
  transition: .4s;
  height: 5px;
  z-index: 1;
  border-radius: 8px 8px 0 0;
}

@media (max-width: 580px) {
  .nav { overflow: auto; }
}

@media only screen and (max-width: 800px) {
.nav, .favicon {
    height: 9vh;
}
.nav-item {
    font-size: 3vw;
    padding: 2vw;
}
.nav-item:before {
    height: 5px;
}
}
